import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileImageListResponse } from "../../Model/Response/FileImageListResponse";
import { FileImageResponse } from "../../Model/Response/FileImageResponse";
import { FileImageViewModel } from '../../Model/ViewModel/FileImageViewModel';
import { FileServicesProvider } from '../file-services/file-services';

/*
  Generated class for the FileImageServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileImageServicesProvider extends FileServicesProvider {
  private FileImages: FileImageListResponse;
  private FileImage: FileImageResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "FileImage";
    console.log("Hello FileImageServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
    if(exec_file_seq===0)
    {
      sub_url = "FileImage?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&order_type={5}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);
    }
    else
    {
      sub_url = "FileImage/Select?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&exec_file_seq={5}&order_type={6}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,exec_file_seq,order_type);
    }
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileImages = new FileImageListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileImages;
    });
  }



  public PostPutAsync(FileImageViewModel:FileImageViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FileImage";
    else
    sub_url = "FileImage/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileImageViewModel.exec_file_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileImageViewModel,mode).map(
      item => {
        this.FileImage = new FileImageResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileImage;
      }
    );
  }
}



