import { FileManagementViewModel } from "./FileManagementViewModel";

export class FileImageViewModel extends FileManagementViewModel {
  /// <summary>
  ///  檔案功能處理主鍵
  /// <summary>
  public file_func_proc_key: string;
  /// <summary>
  ///  檔案功能處理
  /// <summary>
  public file_func_proc: string;

  /// <summary>
  ///  轉換檔案名稱
  /// </summary>
  public convert_to_filename: string;

  /// <summary>
  /// 寬度
  /// </summary>
  public width: number;

  /// <summary>
  /// 高
  /// </summary>
  public height: number;

  /// <summary>
  /// 品質(0~100)
  /// </summary>
  public quality: number;
}



